.Info-2 {
  /* width: ; */

  background: rgba(255, 255, 255, 0.8);
  padding: 3em;
  border-radius: 2em;
  position: relative;
  width: 100%;
  z-index: 1;
  border: solid 2px transparent;
  background-clip: padding-box;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.03);
  box-sizing: border-box;
}

.familiar {
  display: flex;
  flex-direction: row;
}
.icons {
  margin-bottom: 10px;
  margin-top: -20px;
  display: inline-block; /* or any other appropriate display property */
  transition: transform 0.2s;
}
.icons img {
  margin: 10px;
  width: 65px;
}
.profile-2 {
  display: flex;
  flex-direction: column;
  align-items: start;
}
.profile-2 p {
  font-size: 22px;
}
.profile-2 h1{
    font-size: 30px;
}
.profile-2 ul{
  /* width: 360px; */
  border-left: 2px solid #ccc;
  margin-left: 100px;
  line-height: 1.5;
}
.time-line h3{
  font-size: 30px;  
  margin-bottom: -5px;
}
small{
  font-size: 15px;

  
}
ul.timeline {
  list-style-type: none;
  position: relative;
}
ul.time-line:before{
  content: ' ';
    background: var(--gradient-color);
    display: inline-block;
    position: absolute;
    left: 29px;
    width: 2px;
    /* height: 100%; */
    z-index: 1;
    
}
ul.time-line.li::before{
  content: ' ';
    background: var(--gradient-color);
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    left: 20px;
    width: 20px;
    height: 20px;
    z-index: 1;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    top: 0;
    border: 2px solid white;
    background-color: rgb(30, 174, 59);
}

/* hovering efect in icons */
.image {
  display: block;
  width: 100%;
  height: auto;
}

/* The overlay effect - lays on top of the container and over the image */
.overlay {
  position: absolute;
  bottom: 0;
  background: rgb(0, 0, 0);
  background: rgba(0, 0, 0, 0.5); /* Black see-through */
  color: #f1f1f1;
  width: 100%;
  transition: .5s ease;
  opacity:0;
  color: white;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}
.icons:hover{
  transform: scale(1.1);
}

/* When you mouse over the container, fade in the overlay title */
.container:hover .overlay {
  opacity: 1;
}


.timeline {
  width: 100%;
  max-width: 800px;
  margin: 50px auto;
  position: relative;
}

.timeline::before {
  content: '';
  display: block;
  width: 5px;
  height: 100%;
  background-color: #00ffb3;
  position: absolute;
  top: 0;
  left: 11px;
  transform: translateX(-50%);
}

.timeline::after {
  content: '';
  display: table;
  clear: both;
}

.timeline-item {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  background:transparent;
  position: relative;
  margin-left: 30px;
  margin-top: 20px;
  border-radius: 5px; 
  transition: transform 0.2s;
}
.timeline-item:hover{
transform: scale(1.02);
}


.timeline-item h3 {
  margin: 0;
}

.timeline-item small {
  color: black;
}

.timeline-item::before {
  content: '';
  display: inline-block;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  background-color: rgb(255, 0, 115);
  position: absolute;
  left: -28px;
  top: 50%;
  transform: translateY(-50%);
}

.timeline-right .timeline-item::before {
  left: auto;
  right: -15px;
}

  
.timeline-logo {
  float: left;
  margin-right: 20px;
  width: 60px;
  height: auto;
}

.timeline-logo img {
  max-width: 100%;
}

.timeline-edu{
  float: left;

}
.timeline-y{
  float: right;
}


@media screen and (max-width: 800px) {
  .familiar {
    flex-wrap: wrap;
  }
  .Info-2 .profile-2 h1{
    text-align: center;
  }

  .profile-2 ul{
    margin-left: 20px;
    align-items: center;
  }
  .profile-2{
    margin-top: 30px;
  }

  .timeline-logo {
    height: auto;
    margin-top: 15px;
    margin-bottom: 15px;
    max-width: 70px;
  }
  .timeline-logo img{
    width: 70px;
  }
  small{
    display: block;
  }
  .timeline-item h3{
    display: block;
    margin-left: 80px;
    
  }
}
